/**/
::-webkit-scrollbar{
    width: 12px;
    height: 12px;
    padding: 10px 0;
    background-color: $gray-3;
}
/**/
::-webkit-scrollbar-track{
    width: 12px;
    height: 12px;
    
    background-color: $gray-3;
}

/**/
::-webkit-scrollbar-thumb{
    width: 8px;
    min-height: 50px;
    border-radius: 12px;
    background-color: $gray-6;
    &:hover {
        background-color: $blue-5;
    }
}
/**/
::-webkit-scrollbar-button{
    display: none;
}

// IE8
html,body {
  scrollbar-face-color:$gray-3; /*3DThreedFace*/
  scrollbar-highlight-color:$gray-3; /*3DThreedHighlight*/
  scrollbar-shadow-color:$blue-5; /*3DThreedShadow*/
//   scrollbar-3dlight-color:#eeeeee; /**/
//   scrollbar-arrow-color:#000; /* */
  scrollbar-track-color:$gray-6; /*(TrackBar)*/
//   scrollbar-darkshadow-color:#fff; /*ThreedDarkShadow*/ 
}