
html {
  color: $text-1;
  background: #fff;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizelegibility;
}

/*  IE9+ | Chrome | Firefox  <html>  .borderbox  class */
html.borderbox *, html.borderbox *:before, html.borderbox *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/*  */
body, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  margin: 0;
  padding: 0;
}

/*  HTML5 , IE  js  createElement(TAG) */
article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  display: block;
}

/* HTML5  img  */
audio, canvas, video {
  display: inline-block;
}

/*  font  */
body, button, input, select, textarea {
  font-family: Arial, PingFang SC, Lantinghei SC, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}

/* Table cell  */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*  */
fieldset, img {
  border: 0;
}

/* / */
blockquote {
  position: relative;
  color: #999;
  font-weight: 400;
  border-left: 1px solid #2f75a8;
  padding-left: 1em;
  margin: 1em 3em 1em 2em;
}

@media only screen and ( max-width: 640px ) {
  blockquote {
    margin: 1em 0;
  }
}

/* Firefox  */
acronym, abbr {
  border-bottom: 1px dotted;
  font-variant: normal;
}

/*  */
abbr {
  cursor: help;
}

/*  del  */
del {
  text-decoration: line-through;
}

/* , li  */
ul, ol {
  list-style: none;
}

/* ,  */
caption, th {
  text-align: left;
}

q:before, q:after {
  content: '';
}

/*  */
sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
}

:root sub, :root sup {
  vertical-align: baseline; /* for ie9 and other modern browsers */
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

/*  hover  */
a {
  color: $link-color;
  outline: none;
}

a:hover {
  text-decoration: underline;
}

a:focus {
  outline: none;
}

.typo a {
  border-bottom: 1px solid #2f75a8;
}

.typo a:hover {
  border-bottom-color: #555;
  color: #555;
  text-decoration: none;
}

/*  */
ins, a {
  text-decoration: none;
}

/*  u  html5 Draft
 *  class="typo-u" 
 *  <u> http://www.whatwg.org/specs/web-apps/current-work/multipage/text-level-semantics.html#the-u-element
 *  4 http://www.w3.org/TR/html401/appendix/changes.html#idx-deprecated
 *  <u> http://html5doctor.com/u-element/
 */
u, .typo-u {
  text-decoration: underline;
}

/*  */
mark {
  background: #fffdd1;
  border-bottom: 1px solid #ffedce;
  padding: 2px;
  margin: 0 5px;
}

/*  */
pre, code, pre tt {
  font-family: Courier, 'Courier New', monospace;
}

pre {
  background: #f8f8f8;
  border: 1px solid #ddd;
  padding: 1em 1.5em;
  display: block;
  -webkit-overflow-scrolling: touch;
}

/*  horizontal rule */
hr {
  border: none;
  border-bottom: 1px solid #cfcfcf;
  margin-bottom: 0.8em;
  height: 10px;
}

/*  */
small, .typo-small,
  /*  */
figcaption {
  font-size: 0.9em;
  color: #888;
}

strong, b {
  font-weight: bold;
  /*color: #000;*/
}

/*  */
[draggable] {
  cursor: move;
}

.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  zoom: 1;
}

